export default {
  methods: {
    notify(title, body) {
      const description = JSON.parse(body).description
      const notification = new Notification(title, {
        body: description,
        icon: '/icone-180x180.png',
        vibrate: true
      });

      if (body.link) {
        notification.addEventListener('click', () => {
          const link = JSON.parse(body).link
          window.location.href = link
        });
      }
    }
  }
}